export default {
	data() {
		return {
			// 将接受到的数据赋值给当前vue创建的新对象，更改的时候更新当前页面创建的对象内容
			curData: this.item
		}
	},
	// 接收父组件传值
	props: {
		item: Object, // 数据类型
		index: Number, // 数据下标
		swIndex: Number
	},
	methods: {
		// 关注
		tapFocus () {
			this.curData.isFocus = true
			// 官网 API-->界面 --> 交互反馈, 弹出框
			uni.showToast({
			    title: '关注成功',
			});
			this.toParentData()
		},
		// 操作顶和踩
		handleDC (type) {
			switch (type){
				case 'ding':
					// 如果当前状态是顶的状态不处理
					if (this.curData.dcInfo.dcType == '1') {
						return;
					}
					// 如果当前状态是踩，踩的数字--
					if (this.curData.dcInfo.dcType == '2') {
						this.curData.dcInfo.cNum--;
					}
					// 点赞数字加一
					this.curData.dcInfo.dzNum++;
					// 将当前状态变为点赞
					this.curData.dcInfo.dcType = '1'
					this.toParentData()
					break;
				case 'cai':
					// 如果当前状态是踩的状态不处理
					if (this.curData.dcInfo.dcType == '2') {
						return;
					}
					// 如果当前状态是顶，顶的数字--
					if (this.curData.dcInfo.dcType == '1') {
						this.curData.dcInfo.dzNum--;
					}
					// 踩数字加一
					this.curData.dcInfo.cNum++;
					// 将当前状态变为点踩
					this.curData.dcInfo.dcType = '2'
					this.toParentData()
					break;
				default:
					break;
			}
		},
		// 进入详情页
		openDetail () {
			console.log('进入详情页');
		},
		// 返回到父级信息
		toParentData () {
			this.$emit('mergeData', this.curData, this.index, this.swIndex)
		}
	}
}
